  /* width */
::-webkit-scrollbar {
  width: 5px;
  border-radius:1rem;
}

/* Track */
::-webkit-scrollbar-track { 
  background:#f2f2f2;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: grey; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: grey; 
  width:20px;
}





