}
.slider_container{
    width:25rem;
    height:20rem;
    margin-left:1%;
    margin-right:1%;
    -webkit-overflow-scrolling: touch;
    overflow:hidden;     
}
.image_container{
    width:200rem;
    height:20rem;
    position:relative;
    transition:2s;
    display:flex;
    -webkit-overflow-scrolling: touch;
    -webkit-transition: left 2s;
    -moz-transition: left 2s;
    -o-transition: left 2s;
}
.container{
    width:100%;
    padding:0;
    margin:0;
    height:70%;
    margin:0% 1% 0 1%;
    margin-top:2rem;
    border-radius:0.5rem;
    box-shadow:2px 2px 3px #cbced1,
                            -2px -2px 3px #ffffff;
}
.slide_button_container{
    position:relative;
    top:-2opx;
    width:80%;
    margin:0 auto;
    margin-left:20%;
    
}
.slider_button{
    display:inline-block;
    height:10px; 
    width:10px;
    border-radius:5px;
    background-color:brown;
    margin:1%;
    padding:0%;
    margin-bottom:0rem;
}
.slider_button:hover{
    background:orange;
}


#slide_cont_1:target ~ .image_container{
    left:0rem;
}
#slide_cont_2:target ~ .image_container{
    left:0rem;
}
#slide_cont_3:target ~ .image_container{
    left:0rem;
} 
  /* End of Slide Image project */


